import { useTheme } from 'styled-components';
import * as P from './mainSection.parts';

import SectionTitle from '../generics/SectionTitle';
import Quote from '../generics/Quote';

import AboutMeDesc from './AboutMeDesc';
import SkillsWrapper from './SkillsWrapper';

import { QUOTE } from '../../constants';

const MainSection = () => {
    const theme = useTheme();

    return (
        <P.MainSectionPageView bgColor={theme.gray} id="skills">
            <P.MainSectionTitle color={theme.white}>
                kilka słów o mnie ...
            </P.MainSectionTitle>
            <AboutMeDesc />
            <Quote>{QUOTE}</Quote>
            <SkillsWrapper />
        </P.MainSectionPageView>
    );
};

export default MainSection;
